@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.avatar {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.avatar img {
  display: block;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: linear-gradient(to right, #f3e1ed, #f5f3e1);
}

.user-item {
  margin: 1rem;
  width: calc(45% - 2rem);
  min-width: 17.5rem;
}

.user-item a {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding: 1rem;
  color: black;
  background: linear-gradient(to right, #eaafc8, #654ea39b);
}

.user-item a:hover,
.user-item a:active {
  color: #7868e6;
  background: linear-gradient(to right,  #654ea39b, #eaafc8);
}

.user-item__content {  
  padding: 0  !important ;
}



.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.user-item__info h2 {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  color: #000;
}

.user-item__info h3 {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
  color: #000;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #000;
}

.user-item__info h3 {
  margin: 0;
}
.users-list {
  list-style: none;
  margin: 0 auto;
  padding-top: 3%;
  width: 90%;
  max-width: 50rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
